@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,700;0,800;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin:0;
    padding:0;
    font-family: 'Poppins', sans-serif;
}

.main{
    height:100vh;
    width:100%;
    background-image: url("https://wallpapercave.com/wp/Z4CD0Ic.jpg");
    background-size: cover;
    background-repeat:no-repeat;
    display:flex;
    justify-content: center;
    align-items: center;
    
}
.box{
    height:400px;
    width:600px;
    background-color: rgb(251, 251, 255);
    display:flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.55);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 20.0px );
    -webkit-backdrop-filter: blur( 20.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.inputdata{
    margin-bottom: 60px;
}
.inputfield{
    font-weight:bolder;
    font-size:20px;
    outline:none;
    border: none;
    border-bottom: 2px solid black;
    background:none;
}

.fa-search-location{
    font-size: 25px;
    padding-right: 10px;
}

h1{
    font-weight: normal;
}

.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fa-cloud-moon{
    font-size: 50px;
    margin-bottom: 15px;
}

.loc{
    display: flex;
    flex-direction: row;
}
.location{
    font-size: 25px;
}
.fa-map-marker-alt{
    font-size: 30px;
    margin-right: 15px;
}

.temp{
    font-size: 40px;
}
.extrainfo{
    /*background-color:rgb(255, 255, 255);*/
    border-radius: 10px;
    display:flex;
    width:450px;
    height:80px;
    flex-direction:row;
    justify-content:space-around;
    align-items: center;
    margin-top: 15px;
}
.extra{
    display: flex;
    margin-right: 15px;
}

.iconedit{
    margin-right:10px;
    color:crimson;
    font-size:25px;
    margin-bottom: 15px;
}

@media (max-width: 700px) 
{
    .box{
        height:500px;
        width:350px;
    } 
    .inputfield{
       width: 200px;
    }
    .extrainfo{
        width:250px;
        height:200px;
        flex-direction:column;
        margin-top: 10px;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

